function FAQs() {

	return (
		<section className="faq">
			<div className="container">
				<div className="col">
					<h1>FAQs</h1>
					<div className="flex flex-align-top">
						<div className="col">

							<h2>Can I bring my children?</h2>
							<p>Unfortunately, as much as we'd love to invite children, we can only accomodate a few children of close family, and babies under one. We hope that you will understand this decision and that you will still be able to join us on our special day. </p>

							<h2>What if I have dietary requirements?</h2>
							<p>Please note any requirements on the RSVP form, or email us as soon as possible.</p>

							<h2>Is there parking?</h2>
							<p>There is plenty of on-site parking.</p>

							<h2>Can we stay on-site?</h2>
							<p>The venue doesn't have any extra accomodation, but there are plenty of local hotels and taxis, which can be found on the accomodation page.</p>

							<h2>Are you changing your last name/s?</h2>
							<p>Ellie is turning into a Salmon! (Hopefully not literally!!)</p>

							<h2>Can I bring a +1?</h2>
							<p>Unfortuntely,we aren't able to accommodate plus ones. sorry!</p>

							<h2>What time will the reception end?</h2>
							<p>The party will finish at 12pm midnight, with taxis ordered from around 11pm.</p>

							<h2>When is the RSVP deadline?</h2>
							<p>We'd love it if you could let us know before 25th Decemeber 2022.</p>

						</div>
						<div className="col">

							<h2>What time should I arrive?</h2>
							<p>We'll be ready to greet you at 12:00pm, ready for the ceremony to begin at 1pm. If you arrive during the ceremony we'll be happy to laugh about it later!</p>

							<h2>What about gifts?</h2>
							<p>We are not expecting any gifts, so feel free to just turn up and enjoy!.</p>
							<p>If you'd like to kindly gift us something lovely, we've set up a simple fund, which will go towards our honeymoon and a few nice bits we've always wanted for our home.</p>

							<h2>Will there be a cash bar?</h2>
							<p>Wine and champagne will be provided by us until the end of the wedding breakfast (which Ben is just learning is a silly name for lunch). All other drinks can be purchased from the venue bar, which accepts cash or card.</p>

							<h2>What should I wear?</h2>
							<p>Anything except a white dress! We'll be dressing relatively fancy, but you'd fit right in wearing a dress, shirt and chinos, or a fancy suit.</p>
							<p>It's spring time, so dress accordingly! Bright colours are encouraged.</p>

							<h2>Do I need to dress warmly?</h2>
							<p>It is early April, so the weather could go either way! The venue has lovely outdoor and indoor areas - our guests are welcome to explore the beautiful grounds, relax in the outdoor gardens, or snuggle up inside. If it doesn't rain then you'll almost certainly want the option to enjoy the outdoor areas along with the indoor ones.</p>

						</div>

					</div>
				</div>
			</div>
		</section>
	);

}

export default FAQs;
