import { useForm } from "react-hook-form";
import emailjs from "emailjs-com";
import { useState } from "react";

function RSVP() {

	const { register, handleSubmit, formState: { errors } } = useForm();
	const [ hasSubmitted, setHasSubmitted ] = useState();

	const onSubmitYes = data => {
		setHasSubmitted(true);
		//console.log("RSVP yes");
		sendEmailYes(data);
	}
	const onSubmitNo = data => {
		setHasSubmitted(true);
		//console.log("RSVP no");
		sendEmailNo(data);
	}

	function sendEmailYes(data) {		
		emailjs.send("service_rr9kqm2","template_k4bhygb",{
			from_name: data.name,
			from_email: data.email,
			dietary_requirements: data.dietary_requirements,
			message: data.notes,
			}, "lwZ58jPF1fTv1-_lz");
	  }

	  function sendEmailNo(data) {
		emailjs.send("service_rr9kqm2","template_t1cxhi7",{
			from_name: data.name,
			from_email: data.email,
			dietary_requirements: data.dietary_requirements,
			message: data.notes,
			}, "lwZ58jPF1fTv1-_lz");
	  }

	return (
		<section className="rsvp">
			<div className="container flex">
				{hasSubmitted && (
					<div className="col thankyou-box">
						<h1>RSVP</h1>
						<p>Thankyou for RSVP'ing to our special day.</p>
					</div>
				)};

				{!hasSubmitted && (
				<div className="col flex login-box">

					<form>
						<h1>RSVP</h1>

						<div className="user-box">
							<input type="text" name="" required placeholder=" " {...register("name")} />
							<label>Name</label>
						</div>

						<div className="user-box">
							<input required placeholder=" " {...register("email", { required: true })} />
							<label>Email</label>
						</div>

						<div className="user-box">
							<input placeholder=" " {...register("dietary_requirements", { required: false })} />
							<label>Dietary Requirements (optional)</label>
						</div>

						<div className="user-box">
							<textarea placeholder=" " {...register("notes", { required: false })} />
							<label>Notes (optional)</label>
						</div>

						{errors.email && <span>This field is required</span>}


						<div className="button-row">
							<button type="submit" className="color-no" onClick={handleSubmit(onSubmitNo)} > Sadly, no</button>
							<button type="submit"  className="color-yes" onClick={handleSubmit(onSubmitYes)}> Yes!</button>
						</div>

					</form>
				</div>
				)};

				<div className="col grow-15"><img src={require("../img/Tythe_03.jpg")} /></div>
			</div>
		</section>
	);

}

export default RSVP;
