function OurStory() {

	return (
		<>
			<section className="details">
				<div className="container flex">
					<div className="col focus-green">
						<div className="flex-content">
							<h1>Our Story</h1>
							<p>We met in April 2016 on a bright summer day in Oxford. Well... Ellie was in Oxford at least. On her phone. On <i>Plenty of Fish</i>.</p>
							<p>It was love at first swipe, and it took only two dates before Benjamin plucked up the courage to ask Ellie if she "fancied being my girlfriend".</p>
							<p>Almost 7 years, 2 cats, several dozen bottles of red wine later and we're getting hitched!</p>
							<p>Come along and celebrate our love story with food, drinks, music, soppy speeches, and fancy attire!</p>
						</div>
					</div>
					<div className="col"><img src={require("../img/Tythe_02.jpg")} /></div>
				</div>
			</section>
			<section className="details">
				<div className="container flex">
					<div className="col"><img src={require("../img/Tythe_01.jpg")} /></div>
					<div className="col focus-grey">
						<div className="flex-content">
							<h1>Our future</h1>
							<p>House, Kids, Cats... maybe a dog. We'd love to live a little further into the country, enjoy spacious living, a warm hearth, and raise a family.</p>
							<p>If it all goes to plan, then ten years from now you'll find us cuddled on the sofa with some small ones at our feet, or out exploring the local wildlife!</p>
							<p>Twenty years from now you might find Ben tinkering around his wood workshop, or Ellie crafting with lots of glitter.</p>
						</div>
					</div>
				</div>
			</section>
		</>
	);

}

export default OurStory;
